html {
    font-size: 100%;
    box-sizing: border-box;
}

// ensures padding is included in the total width. not in addition too.
// content-box is another option
*, 
*::before, 
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background: var(--background);
    color: var(--text-color1);
		min-height: 150vh;
}

.visually-hidden {
    position: absolute;
    left:     -10000px;
    top:      auto;
    width:    1px;
    height:   1px;
    overflow: hidden;
  }